



































import Vue from 'vue';

export default Vue.extend({
  name: 'CodeBlock',
  props: {
    name: {
      required: true,
      type: String,
    },
    code: {
      required: true,
      type: String,
    },
  },
  data: () => ({
    copied: false,
  }),
  methods: {
    onCopyOptionsJson() {
      this.$copyText(this.code);

      this.copied = true;
    },
    onCopiedClose() {
      this.copied = false;
    },
  },
});
